@import url(https://fonts.googleapis.com/css2?family=Dela+Gothic+One&display=swap);
*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
nav {
  font-family: "font-family: 'Dela Gothic One', cursive" !important;
  background-color: rgb(174, 98, 245) !important;
}
.navbar-brand {
  font-size: 1.8rem !important;
  font-weight: bold;
  color: white !important;
  text-shadow: 0 5px 5px black;
}
.nav-active {
  border-bottom: 3px solid white;
}
.nav-link {
  margin-left: 10px;
  color: white !important;
  font-size: 1.5rem;
  font-weight: bold;
  text-shadow: 0 2px 2px rgb(56, 55, 55);
}
button {
  background-color: white !important;
  outline: none;
}
@media only screen and (max-width: 600px) {
  .nav-link {
    text-align: center;
  }
  .nav-active {
    border-bottom: 0;
    color: rgb(242, 6, 250) !important;
  }
}

.brand-name {
  color: rgb(174, 98, 245) !important;
}
#header {
  width: 100%;
  height: 80vh;
  margin-top: 40px;
}
.header-img {
  text-align: right;
}
.animated {
  animation: up-down 2s ease-in-out infinite alternate-reverse both;
}
@-webkit-keyframes up-down {
  0% {
    -webkit-transform: translateY(10px);
            transform: translateY(10px);
  }
  100% {
    -webkit-transform: translateY(-10px);
            transform: translateY(-10px);
  }
}
@keyframes up-down {
  0% {
    -webkit-transform: translateY(10px);
            transform: translateY(10px);
  }
  100% {
    -webkit-transform: translateY(-10px);
            transform: translateY(-10px);
  }
}
.btn-outline-primary {
  color: rgb(174, 98, 245) !important;
  border: 2px solid rgb(174, 98, 245) !important;
}
.btn-outline-primary:hover {
  background-color: rgb(174, 98, 245) !important;
  color: white !important;
}



.card:hover {
  -webkit-animation: move 2s ease-in-out infinite alternate both;
  animation: move 2s ease-in-out infinite alternate both;
  box-shadow: 0px 5px 5px rgb(174, 98, 245);
}
@keyframes move {
  0% {
    -webkit-transform: translateY(0);
            transform: translateY(0);
  }
  100% {
    -webkit-transform: translateY(-15px);
            transform: translateY(-15px);
  }
}
@-webkit-keyframes move {
  0% {
    -webkit-transform: translateY(0px);
            transform: translateY(0px);
  }
  100% {
    -webkit-transform: translateY(-15px);
            transform: translateY(-15px);
  }
}

.footer {
  margin-top: 10%;
  background-color: rgb(174, 98, 245) !important;
  text-shadow: 0 2px 2px black;
}
.fa-facebook {
  color: rgb(59, 91, 152);
}
.fa-linkedin {
  color: #0e76a8;
}

.fa-twitter {
  color: #1da1f2;
}

.fa-whatsapp {
  color: #25d366;
}
.fa-instagram {
  color: rgb(193, 53, 132);
}
.fa-envelope {
  color: rgb(255, 92, 92);
}

